import { ServicePluginDefinition } from '@wix/sdk-types';
import {
  Context,
  ListEligibleMembershipsRequest,
  ListEligibleMembershipsResponse,
  ChargeMembershipRequest,
  ChargeMembershipResponse,
  GetMembershipVoidabilityRequest,
  GetMembershipVoidabilityResponse,
  VoidMembershipChargeRequest,
  VoidMembershipChargeResponse,
} from './service-plugins-types.js';
import { transformSDKTimestampToRESTTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformRESTTimestampToSDKTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformPaths } from '@wix/sdk-runtime/transformations/transform-paths';
import {
  renameKeysFromSDKRequestToRESTRequest,
  renameKeysFromRESTResponseToSDKResponse,
} from '@wix/sdk-runtime/rename-all-nested-keys';

export interface ListEligibleMembershipsEnvelope {
  request: ListEligibleMembershipsRequest;
  metadata: Context;
}

export interface ChargeMembershipEnvelope {
  request: ChargeMembershipRequest;
  metadata: Context;
}

export interface GetMembershipVoidabilityEnvelope {
  request: GetMembershipVoidabilityRequest;
  metadata: Context;
}

export interface VoidMembershipChargeEnvelope {
  request: VoidMembershipChargeRequest;
  metadata: Context;
}

export const provideHandlers = ServicePluginDefinition<{
  /**
   *
   * This method retrieves eligible memberships from your app. */
  listEligibleMemberships(
    payload: ListEligibleMembershipsEnvelope
  ): ListEligibleMembershipsResponse | Promise<ListEligibleMembershipsResponse>;

  /**
   *
   * This method requests that a membership be charged by your app. */
  chargeMembership(
    payload: ChargeMembershipEnvelope
  ): ChargeMembershipResponse | Promise<ChargeMembershipResponse>;

  /**
   *
   * This method retrieves from your app whether a membership can be voided. */
  getMembershipVoidability(
    payload: GetMembershipVoidabilityEnvelope
  ):
    | GetMembershipVoidabilityResponse
    | Promise<GetMembershipVoidabilityResponse>;

  /**
   *
   * This method requests that a membership charge be voided by your app. */
  voidMembershipCharge(
    payload: VoidMembershipChargeEnvelope
  ): VoidMembershipChargeResponse | Promise<VoidMembershipChargeResponse>;
}>('ECOM_MEMBERSHIPS', [
  {
    name: 'listEligibleMemberships',
    primaryHttpMappingPath: '/v1/list-eligible-memberships',
    transformations: {
      toREST: (payload: any) => {
        const toRestResponse = transformPaths(payload, [
          {
            transformFn: transformSDKTimestampToRESTTimestamp,
            paths: [
              { path: 'eligibleMemberships.expirationDate' },
              { path: 'invalidMemberships.membership.expirationDate' },
            ],
          },
        ]);

        return renameKeysFromSDKRequestToRESTRequest(toRestResponse);
      },
      fromREST: (payload: any) => {
        const fromRestRequest = transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'request.lineItems.serviceProperties.scheduledDate' },
            ],
          },
        ]);

        return renameKeysFromRESTResponseToSDKResponse(fromRestRequest);
      },
    },
  },

  {
    name: 'chargeMembership',
    primaryHttpMappingPath: '/v1/charge-membership',
    transformations: {
      toREST: (payload: any) => {
        const toRestResponse = payload;

        return renameKeysFromSDKRequestToRESTRequest(toRestResponse);
      },
      fromREST: (payload: any) => {
        const fromRestRequest = transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [{ path: 'request.serviceProperties.scheduledDate' }],
          },
        ]);

        return renameKeysFromRESTResponseToSDKResponse(fromRestRequest);
      },
    },
  },

  {
    name: 'getMembershipVoidability',
    primaryHttpMappingPath: '/v1/get-voidability',
    transformations: {
      toREST: (payload: any) => {
        const toRestResponse = payload;

        return renameKeysFromSDKRequestToRESTRequest(toRestResponse);
      },
      fromREST: (payload: any) => {
        const fromRestRequest = payload;

        return renameKeysFromRESTResponseToSDKResponse(fromRestRequest);
      },
    },
  },

  {
    name: 'voidMembershipCharge',
    primaryHttpMappingPath: '/v1/void-membership-charge',
    transformations: {
      toREST: (payload: any) => {
        const toRestResponse = payload;

        return renameKeysFromSDKRequestToRESTRequest(toRestResponse);
      },
      fromREST: (payload: any) => {
        const fromRestRequest = payload;

        return renameKeysFromRESTResponseToSDKResponse(fromRestRequest);
      },
    },
  },
]);
